<template>
    <div id="subList">
        <div class="item" v-for="(item, index) in subList" :key="index">
             <img v-lazy="item.avatarUrl" alt="">
             <div class="nickName">{{item.nickname}}</div>
        </div>
    </div>
</template>
<script>
import { req_playList_subscribers } from "../../network/song/index.js";
export default {
  name: "subList",
  props: {
      id: String,
  },
  data() {
    return {
      // 获取收藏者
      subList: [],
      subNum: 24,
    };
  },
  created() {
      this.getSubscribers();
  },
  methods: {
    // 获取歌单收藏者
    getSubscribers() {
      req_playList_subscribers(this, {
          id: this.id,
          limit: this.subNum,
      }).then(res => {
        this.subList = res.subscribers;
      });
    },
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
#subList {
    @include flex();
    flex-wrap: wrap;
    .item {
        width: 100px;
        height: 140px;
        background-color: $colorE;
        margin-right: 1px;
        margin-bottom: 10px;
        &:nth-child(9n) {
          margin-right: 0px;
        }
        img {
            width: 100px;
            height: 100px;
            border-radius: 50%;
        }
        .nickName {
            text-align: center;
            @include font(12px, 16px, $colorC);
            margin-top: 12px;
            cursor: pointer;
        }
    }
}
</style>