<template>
  <div id="playlist">
    <div class="music-container">
      <!-- 主体内容 -->
      <div class="playlist-container">
        <!-- 歌单头部 -->
        <div class="playlist-header">
          <img v-lazy="playListDetail.coverImgUrl" alt="">
          <div class="info">
               <div class="name">{{playListDetail.name}}</div>
               <div class="btn-group" v-if="btnFlag">
                <div class="btn">
                  <div class="btn_mask">播放量</div>
                  {{playListDetail.playCount}}
                </div> 
                <div class="btn">
                  <div class="btn_mask">发表时间</div> 
                  {{playListDetail.createTime}}
                </div>
                <div class="btn">
                  <div class="btn_mask">收藏者数量</div> 
                  {{playListDetail.subscribedCount}}
                </div>
                <div class="btn play" @click="playAll">播放全部</div> 
               </div>
               <div class="description">{{playListDetail.description}}</div>
          </div>
        </div>
        <!-- 歌单导航条 -->
        <div class="playlist-nav">
            <div :class="['playlist-name','nav-item', type == 1? 'active': '']" @click="type=1">歌单（{{playListDetail.trackCount}}）</div> 
            <div :class="['commit', 'nav-item',  type == 2? 'active': '']" @click="type=2">评论（{{playListDetail.commentCount}}）</div>
            <div :class="['sub', 'nav-item', type == 3? 'active': '']" @click="type=3">收藏者（{{playListDetail.subscribedCount}}）</div>
        </div>
        <!-- 主体内容 -->
        <div :class="['content-container', type == 1? 'isscroll':'']">
          <!-- 歌单 -->
          <song-list v-show="type==1" :trackIds="trackIds" :trackCount = "playListDetail.trackCount" :scroll="scroll" @play="play"/>
          <!-- 评论 -->
          <song-comment-list v-show="type==2" :id="id" :total="playListDetail.commentCount"/>
          <!-- 收藏者 -->
          <sub-list v-show="type==3" :id="id"/>
        </div>
      </div>
    </div>
    <!-- 音乐播放器 -->
    <transition name="prograss">
         <music-prograss  class="music-prograss" v-show="musicPrograss"></music-prograss>
    </transition>
  </div>
</template>
<script>
// http://localhost:8080/#/playlist/1634622709328?id=5462340807

// 工具包
import { req_playList_in_songAllid, req_checkMusic } from "../network/song/index.js";
import tools from "../tools/formatData.js";

// 组件包
import songList from "../components/playlist-components/songList";
import songCommentList from "../components/playlist-components/songComment.vue";
import subList from "../components/playlist-components/subList";
import musicPrograss from "../components/musicPrograss";
export default {
  name: "playlist",
  components: {
    songList,
    songCommentList,
    subList,
    musicPrograss,
  },
  data() {
    return {
      // 滚轮对象
      scrollObj: [],
      // 歌单 id
      id: this.$route.query.id,
      // 1 歌单  2 评论  3 收藏者
      type: 1, 
      // 歌曲id
      trackIds: [],
      // 歌单信息
      playListDetail: {},
      // 是否触底 （产生随机数）
      scroll: 1,
      // 按钮组
      btnFlag: false,

      // 正在播放的歌曲 id
      playSongId: 0,
      // 正在播放的歌曲名字
      playSongname: "",
      // 正在播放的歌曲时长
      songTime: 0,
      // 歌曲进度条
      musicPrograss: this.$store.state.musicPrograss,
    };
  },
  // 页面渲染前调用
  created() {
    this.getsong();
  },
  watch: {
    playListDetail: function() {
      // 格式化播放数量
      this.playListDetail.playCount = tools._tranNumber(
        this.playListDetail.playCount,
      );
      // 格式化日期
      this.playListDetail.createTime = tools._tranDate(
        this.playListDetail.createTime
      );
      this.btnFlag = true;
    },
    type: function() {
       if(this.type != 1) {
          // 隐藏音乐进度条
          this.$store.dispatch("saveMusicPrograss", false);
       }else {
          if(window.localStorage.getItem("currentPlay")) {
             // 显示音乐进度条
             this.$store.dispatch("saveMusicPrograss", true);
          }
          // 禁止重新加载音乐
          this.$store.dispatch("saveMusicLoading", false);
       }
    },
    // 监听 vuex 中 loading 的变化， 使其能够动态赋值给 data 数据中
    '$store.state.musicPrograss': function() {
       this.musicPrograss = this.$store.state.musicPrograss;
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    // 初始化
    init() {
      document.documentElement.scrollTop = 0;
      if (this.type == 1) {
        this.scrollObj = document.querySelector(".content-container");
        this.scrollObj.onscroll = () => {
        
          // 滚动视口高度(也就是当前元素的真实高度)
          let scrollHeight = this.scrollObj.scrollHeight || this.scrollObj.scrollHeight;
          // 可见区域高度
          let clientHeight = this.scrollObj.clientHeight;
          // 滚动条顶部到浏览器顶部高度
          let scrollTop = this.scrollObj.scrollTop;
          if (clientHeight + Math.ceil(scrollTop) == scrollHeight) {
             let value = Math.random(); 
             // 触发子组件加载数据
             value = value == this.scroll ? value + 1 : value 
             this.scroll = value;
          }
        };
      }
    },
    // 获取歌单中歌曲的所有 id 及 歌单简介
    getsong() {
      let params = {
        id: this.id,
      };
      req_playList_in_songAllid(this, params)
        .then(res => {
          this.playListDetail = res.playlist;
          this.trackIds = res.playlist.trackIds;
        
        })
        .catch(error => {
          console.log("获取歌单详情出现错误", error);
        });
    },
    // 播放全部
    playAll() {
      let time = new Date().getTime();
      this.$router.push({
        path: `/song/${time}`
      })
      // 存入本地缓存
      window.localStorage.setItem("playList", JSON.stringify(this.trackIds));
      window.localStorage.setItem("playListCover", this.playListDetail.coverImgUrl);
      window.localStorage.setItem("trackCount", this.playListDetail.trackCount)
    },
    // 播放
    play(id, songName, dt) {
      //   判断歌曲是否可以播放
      req_checkMusic(this, {id}).then(res => {
        let result = res.success
        if(result) {
           let currentPlay = {
             playSongId: id,
             playSongname: songName,
             songTime: dt,
           }
           //  存入缓存
           window.localStorage.setItem("currentPlay", JSON.stringify(currentPlay));
          //  重新加载音乐
          this.$store.dispatch("saveMusicLoading", Math.random());
          //  触发音乐滚动条
          this.$store.dispatch("saveMusicPrograss", true);
        }
      }).catch(() => {
          this.$message({
            showClose: true,
            message: '亲爱的,暂无版权',
            type: 'error'
           });
          //  关闭掉加载
          this.$store.dispatch("saveLoadingFlag", false);
      }) 
    }
  },
  beforeDestroy() {
    // 清除监听器
    this.scrollObj.onscroll = null;
   
  }
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
@import "../assets/scss/scroll.scss";
#playlist {
  position: relative;
  background-color: #fafafa;
  .music-container {
    .playlist-container {
      width: 900px;
      margin: 0px auto 0px auto;
      padding-bottom: 20px;
      .playlist-header {
        border: 1px solid rgba(0, 0, 0, 0.3);
        @include flex();
        img {
          display: block;
          width: 300px;
          height: 300px;
        }
        .info {
          flex: 1;
          height: 300px;
          box-sizing: border-box;
          padding-left: 45px;
          .name {
            @include font(24px, 40px, $colorB);
            padding: 12px 0px 12px 40px;
            width: 515px;
          }
          .description {
            height: 120px;
            padding: 0px 12px;
            box-sizing: border-box;
            @include font(16px, 30px, $colorC);
            @include fontDHidden(4);
            cursor: pointer;
          }
          .btn-group {
            @include flex(flex-start);
            margin-left: 40px;
            margin-bottom: 20px;
            .btn {
              margin-right: 12px;
              width: 100px;
              height: 40px;
              background-color: #f2af00;
              line-height: 40.8px;
              text-align: center;
              color: #fff;
              position: relative;
              outline: none;
              border: none;
              &:hover {
                cursor: pointer;
                .btn_mask {
                  transform: rotateX(90deg);
                }
              }
              .btn_mask {
                background: $colorA;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                transition: all 0.3s;
              }
            }
            .play {
              background-color: $colorA;
            }
          }
        }
      }
      .playlist-nav {
        margin-top: 20px;
        margin-bottom: 12px;
        border: 1px solid $colorA;
        @include flex(flex-start);
        .nav-item {
          flex: 1;
          height: 30px;
          text-align: center;
          @include font(16px, 30px, $colorB);
          cursor: pointer;
          &:nth-child(2) {
            border-left: 1px solid $colorA;
            border-right: 1px solid $colorA;
          }
        }
        .active {
          color: $colorD;
          background-color: $colorA;
        }
      }
      .isscroll {
        height: 800px;
        overflow-y: auto;
      }
      .content-container {
        width: 900px;
        margin: 0px auto;
      }
    }
  }
  .music-prograss {
    position: fixed;
    bottom: -100px;
    left: 0;
    right: 0;
    margin-right: auto;
    margin-left: auto;
  } 
  .prograss-enter {
      bottom:-172px;
  }
  .prograss-enter-to {
    bottom: -100px;
  }
  .prograss-leave-to {
     bottom:-172px;
  }
  .prograss-enter-active , /*过程*/
  .prograss-leave-active {
      transition: bottom .5s;
    }
}
</style>