<template>
  <div id="commentList">
    <comment-input :count="total"></comment-input>
    <comment
      class="item"
      v-for="(item, index) in commentList"
      :key="index"
      :avatarUrl="item.user.avatarUrl"
      :nickname="item.user.nickname"
      :content="item.content"
      :time="item.time"
    ></comment>
      <div
        class="pagination"
        v-if="pagflag"
      >
        <el-pagination
          :background="true"
          layout="total, sizes, prev, pager, next ,jumper"
          :page-size="commentNum"
          :page-sizes="[5, 10, 20]"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
          </el-pagination>
  </div>
  </div>
</template>
<script>
import { req_song_comment } from "../../network/comment/index.js";
import tools from "../../tools/formatData.js";

import commentInput from "../commentInput";
import comment from "../comment";
export default {
  name: "commentList",
  components: {
    commentInput,
    comment,
  },
  props: {
    id: String,
    total: Number,
  },
  data() {
    return {
      pagflag: false,
      // 评论信息
      commentList: [],
      commentNum: 10,
      commentPage: 0,
    };
  },
  watch: {
    commentList: function() {
      this.commentList.forEach((e, index, arr) => {
        let value = tools._tranDate(e.time);
        arr[index].time = value;
      });
      this.pagflag = true;
    },
  },
  created() {
    this.getcomment();
  },
  methods: {
    // 获取歌单评论
    getcomment() {
      let offset;
      if (this.commentPage == 0) {
        // 排除掉页面第一次请求
        offset = this.commentNum * this.commentPage;
      } else {
        offset = this.commentNum * (this.commentPage - 1);
      }
      req_song_comment(this, {
         id: this.id,
         limit: this.commentNum,
         offset,
      }).then(res => {
        this.commentList = res.comments;
        // 设置当前页数
        this.commentPage++;
      });
    },
    // 监听 页大小 改变事件
    handleSizeChange(newsize) {
      // 最新的条数（newsize）赋值给 动态的 commentNum
      this.commentNum = newsize;
      //获取到最新一页显示的数据
      this.getcomment();
    },
    // 监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      console.log(newPage);
      //把最新的页码（newPage）赋值给 动态的 commentPage
      this.commentPage = newPage;
      //获取到最新显示的页码值  重新发送axios请求
      this.getcomment();
    },
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
.el-pagination {
  margin-right: auto;
  margin-left: auto;
}
.pagination {
  @include flex();
}
.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: $colorA;
  color: #fff;
}
</style>